
$container-sm : 1020px;
$container-md : 1020px;
$container : 1020px;
$container-lg : 1020px;
/* 초기화 */
html {overflow-y:scroll}
body {margin:0;padding:0;font-size:0.75em;font-family:'Pretendard', dotum, sans-serif;background:#fff}
html, h1, h2, h3, h4, h5, h6, form, fieldset, img {margin:0;padding:0;border:0}
h1, h2, h3, h4, h5, h6 {font-size:1em;font-family:'Pretendard', dotum, sans-serif}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {display:block}

ul, dl,dt,dd {margin:0;padding:0;list-style:none}
legend {position:absolute;margin:0;padding:0;font-size:0;line-height:0;text-indent:-9999em;overflow:hidden}
label, input, button, select, img {vertical-align:middle;font-size:1em}
input, button {margin:0;padding:0;font-family:'Pretendard', dotum, sans-serif;font-size:1em}
input[type="submit"] {cursor:pointer}
button {cursor:pointer}

textarea, select {font-family:'Pretendard', dotum, sans-serif;font-size:1em}
select {margin:0}
p {margin:0;padding:0;word-break:break-all}
hr {display:none}
pre {overflow-x:scroll;font-size:1.1em}
a {color:#000;text-decoration:none}

*, :after, :before {
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  box-sizing:border-box;
}

input[type=text],input[type=password], textarea {
-webkit-transition:all 0.30s ease-in-out;
-moz-transition:all 0.30s ease-in-out;
-ms-transition:all 0.30s ease-in-out;
-o-transition:all 0.30s ease-in-out;
outline:none;
}

input[type=text]:focus,input[type=password]:focus, textarea:focus,select:focus {
-webkit-box-shadow:0 0 5px #9ed4ff;
-moz-box-shadow:0 0 5px #9ed4ff;
box-shadow:0 0 5px #9ed4ff;
border:1px solid #558ab7 !important;
}

img{
  vertical-align: top;
  max-width: 100%;
}


body,
html{
  background-color: #000;
}

.labs{
  &-container{
    max-width: 1180px;
    margin: 0 auto;
    position: relative;
  }
  &-row{}
  &-header{}
  &-content{}
  &-content{}
  &-section{
    &-title{
      
    }
  }
  
}
.labs-header{
  position: fixed;
  z-index: 1001;
  left: 0;
  right: 0;
  top: 50px;
  padding: 0 15px;
  .labs-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.logos{
  a{
    display: block;
    width: 80px;
    height: 43px;
    background: url(../images/logo.svg);
    background-size: 100% auto;
  }
}
.gnb-wraps{

}
.gnb-move{
  display: flex;
  align-items: center;
  gap: 0 8px;
}
.gnb-move-item{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border:1px solid #7D4EA5;
  color: #7D4EA5;
  font-family: 'Groovity';
  padding: 2px 14px;
  line-height: 1;
  font-size: 20px;
  border-radius: 50px;
  span{
    text-transform: uppercase;
  }
}


.labs-section{
  padding-top: 140px;
  &-title{
    padding-bottom: 100px;
    h2{
      color: #fff;
      font-family: Groovity;
      font-size: 64px;
      font-weight: 400;
      line-height: 70.4px;
      text-align: center;
    }
  }
}

.labs-home{
  background: #DED9D6;
  .wraps{
    background: url(../images/cracker_bg.png) no-repeat 50% 100%;
    height: 1001px;
  }
  .monster{
    margin: 0 auto;
    width: 1004px;
    padding-top: 280px;
  }
}
.labs-making{
  .labs{
    &-container{
      display: grid;
      grid-template-columns: repeat(3, minmax(0,1fr));
      grid-gap: 50px 30px;
    }
  }
}
.labs-card{
  &-img{
    overflow: hidden;
    position: relative;
    img{
      object-fit: cover;
      object-position: center center;
    }
  }
  &-content{
    text-align: center;
    h3{
      color: #fff;
      font-family: Noto Sans CJK KR;
      font-size: 20px;
      font-weight: 700;
      line-height: 29.6px;
      text-align: center;
      padding: 30px 0 11px;
    }
    p{
      font-family: Noto Sans CJK KR;
      font-size: 12px;
      font-weight: 400;
      line-height: 17.76px;
      text-align: center;
      color: #fff;
    }
  }
}

.labs-ventures{
  .labs-container{
    display: grid;
    grid-template-columns: repeat(3, minmax(0,1fr));
    grid-gap: 50px 20px;
    
  }
  .labs-picture{
    .ficture{
      text-align: center;
      img{
        width: 100%;
        aspect-ratio: 1 / 1;
        object-fit: cover;
        object-position: center center;
      }
    }
  }
  .description{
    text-align: center;
    color: #fff;
    h3{
      font-family: Noto Sans CJK KR;
      font-size: 20px;
      font-weight: 700;
      line-height: 29.6px;
      text-align: center;
    }
    p{
      font-family: Noto Sans CJK KR;
      font-size: 12px;
      font-weight: 400;
      line-height: 17.76px;
      text-align: center;
      margin-top: 7px;
    }
  }
}
.labs-news{
  .labs-container{
    max-width: 1420px;
    height: 1535px;
    background: url(../images/news_cracker1.png);
  }
}
.labs-sw{
  margin: 0 auto;
  padding-top: 257px;
  max-width: 580px;
}

.labs-portfolio{
  .labs-container{
    display: grid;
    grid-template-columns: repeat(4, minmax(0,1fr));
    grid-gap: 120px 135px;
  }
}

.contact-wrap{
  margin: 0 auto;
  width: 1230px;
  height: 954px;
  background: url(../images/contact.png) no-repeat 50% 50%;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-list{
  display: flex;
  flex-direction: column;
  gap: 14px 0;
  width: 100%;
  padding: 0 240px 0 100px;
  li{
    display: flex;
    align-items: center;
    strong{
      color: #874DAC;
      text-transform: uppercase;
      flex-shrink: 0;
      width: 280px;
      font-size: 45px;
    }
    .fut{
      flex-grow: 1;
      min-width: 0;
    }
    .input{
      border: 0;
      background: rgba(125, 78, 165, 0.2);
      color: #000;
      height: 50px;
      border-radius: 10px;
      width: 100%;
      padding: 0 15px;
    }
  }
  .en{
    padding-top: 48px;
  }
  .fut{
    &.center{
      text-align: center;
    }
    #submitBtn{
      border: 0;
      background: rgba(125, 78, 165, 1);
      width: 184px;
      height: 48px;
      border-radius: 20px;
      color: #fff;
      text-transform: uppercase;
      font-size: 40px;
    }
  }
}
body{
  padding-bottom: 120px;
}